.header{
    display: flex;
    justify-content: flex-end;
    background-color: rgb(248, 178, 131);
    padding: 30px 0 30px 0;

}
.header__user{
    color: white;
    font-size: 1.2em;
}
.header__button{
    border-color: antiquewhite;
    font-size: 1.2em;
    margin: 20px 10px;
    padding: 10px 15px;
    border-radius: 10px;
    text-decoration: none;
    color: white;
    border: 2px solid;
}
@media (max-width: 375px){
    .header__button{
        font-size: 1em;  
    }
    .header__user{
        
        font-size: 1em;
    }
}