.login{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
}
.login__form{
    width: 300px;
    padding: 32px;
    border-radius: 10px;
    box-shadow: 0 4px 16px #ccc;
    letter-spacing: 1px;
}
.login__input{
    width: 100%;
    padding: 0 0 10px 0;
    border: none;
    border-bottom: 1px solid #e0e0e0;
    outline: none;
    background: transparent; 
    transition: 0.5s;
    margin-bottom: 30px;
}
.login__input:focus{
    border-bottom: 1px solid #1a73a8;
}
.login__title{text-align: center;}
.login__btn a{
    border-color: antiquewhite;
    font-size: 1.2em;
    color: white;
    border: 2px solid;
    background-color: rgb(248, 178, 131);
    border-radius: 10px;
    padding: 10px 15px;
    margin: 0 auto;
    text-decoration: none;
}
.login__btn{
    display: flex; 
    justify-content: center;
}