img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.image-grid {
	display: grid;
	gap: 0.5rem;
	grid-template-columns: repeat(auto-fill, minmax(450px, 1fr));
}
@media (max-width: 1024px){
    .image-grid {
        grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    }
}
.unlike{
    position: relative;
    opacity: 0.8;
}


.gallery__card-unlike{
    position: relative; 
    background-color: red;
}

.gallery__card{
    position: relative;
    
    
}
.gallery__like{
    font-size:150px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    z-index: 1;
    color : red;
    opacity: 0;
    cursor: pointer;
}
.gallery__like:hover{
    opacity: 1;
}
form{
    margin: 30px auto 30px;
    text-align: center;
  }
  label input{
    height: 0;
    width: 0;
    opacity: 0;
  }
  label{
    display: block;
    width: 30px;
    height: 30px;
    border: 1px solid rgb(248, 178, 131);
    border-radius: 50%;
    margin: 10px auto;
    line-height: 25px;
    color:rgb(248, 178, 131);
    font-weight: bold;
    font-size: 30px;
    cursor: pointer;
  }
  label:hover{
    background: rgb(248, 178, 131);
    color: white;
    transition-duration: 1s;
  }
  
.gallery__btn button{
    border-color: antiquewhite;
    font-size: 1.2em;
    color: white;
    border: 2px solid;
    background-color: rgb(248, 178, 131);
    border-radius: 10px;
    padding: 15px;
}
.gallery__btn{
    display: flex;
    justify-content: center;
    margin: 30px;
}